<template>
  <v-app>
    <v-app-bar
      absolute
      color="#6A76AB"
      dark
      shrink-on-scroll
      prominent
      height="56"
      src="https://picsum.photos/1920/1080?random"
      fade-img-on-scroll
      scroll-target="#scrolling-techniques-3"
      >
      <template v-slot:img="{ props }">
        <v-img
          v-bind="props"
          gradient="to top right, rgba(100,115,201,.7), rgba(25,32,72,.7)"
        ></v-img>
      </template>

      <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->

      <v-app-bar-title>Publicación de convocatorias</v-app-bar-title>

      <v-spacer></v-spacer>

      <v-btn text @click="adminApp">Administración<v-icon right>admin_panel_settings</v-icon></v-btn>
      <v-btn text @click="$router.push({ name: 'pqrsdf' })">PQRSDF<v-icon right>mdi-school</v-icon></v-btn>
      <v-btn text>Buscar<v-icon right>mdi-magnify</v-icon></v-btn>

      <v-btn text @click="closeSession">salir<v-icon right>mdi-open-in-new</v-icon></v-btn>
      <template v-slot:extension>
        <div class="my-2" v-show="$session.get('logged') && $store.state.appActiva=='ADM'">
          <span class="subtitle-1">{{`Usuario: @${$session.get('usuario')}`}}</span>
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn icon medium v-on="on"  @click="cerrarsesionAdmin">
                        <v-icon>exit_to_app</v-icon>
                    </v-btn>
                </template>
                <span>Cerrar sesión</span>
            </v-tooltip>
        </div>
        <v-tabs align-with-title :hide-slider="inPqrsdf" v-model="tab" v-if="$store.state.appActiva=='USR'">
          <v-tab @click="$router.push({ name: 'home' })" key="R">Registro Básico</v-tab>
          <v-tab @click="$router.push({ name: 'anuncios' })" key="A">Anuncios de procesos de adquisición</v-tab>
          <v-tab @click="$router.push({ name: 'adjudicaciones' })" key="C">Adjudicaciones de contratos</v-tab>
          <v-tab @click="$router.push({ name: 'misprocesos' })" key="P">Mis procesos de adquisición</v-tab>
          <v-tab @click="$router.push({ name: 'miscontratos' })" key="T">Mis Contratos</v-tab>
        </v-tabs>
        <!-- <v-tabs-items
          v-model="tab"
          >
          <v-tab-item>

          </v-tab-item>
        </v-tabs-items> -->
      </template>
    </v-app-bar>
    <v-main style="margin-top: 180px" class="container">
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>
import { getAuth } from 'firebase/auth'
export default {
  name: 'App',
  data () {
    return {
      tab: null
    }
  },
  methods: {
    closeSession () {
      const auth = getAuth()
      auth.signOut().then(() => {
        this.$store.commit('setAppActiva', 'USR')
        this.$router.push('/autenticacion')
      })
    },
    adminApp () {
      const auth = getAuth()
      auth.signOut().then(() => {
        this.$store.commit('setAppActiva', 'ADM')
        if (this.$session.exists()) {
          this.$router.push('/principalAdminApp')
        } else {
          this.$router.push('/autenticaLoginApp')
        }
      })
    },
    cerrarsesionAdmin () {
      this.$session.destroy()
      this.$store.commit('setAppActiva', 'ADM')
      this.$router.push('/autenticaLoginApp')
    }
  },
  computed: {
    inPqrsdf () {
      const path = this.$route.path
      return (path === '/pqrsdf')
    }
  },
  mounted () {
    this.tab = 'R'
    // this.$router.push({ name: 'home' })
  }
}
</script>
<style>
  .v-app-bar-title__content {
    min-width: 450px;
  }
</style>
