import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
// import { auth } from 'firebase-admin'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    component: () => import(/* webpackChunkName: "home" */ '../views/registro/RegistroBasico.vue')
  },
  {
    path: '/autenticacion',
    name: 'auth',
    component: () => import('../views/autenticacion/Autenticacion.vue')
  },
  {
    path: '/anuncios_procesos_adquisicion',
    name: 'anuncios',
    component: () => import(/* webpackChunkName: "anuncios" */ '../views/anuncios/Anuncios.vue')
  },
  {
    path: '/adjudicaciones_contratos',
    name: 'adjudicaciones',
    component: () => import(/* webpackChunkName: "adjudicaciones" */ '../views/adjudicaciones/Adjudicaciones.vue')
  },
  {
    path: '/mis_procesos_adquisicion',
    name: 'misprocesos',
    component: () => import(/* webpackChunkName: "misprocesos" */ '../views/misprocesos/MisProcesos.vue')
  },
  {
    path: '/pqrsdf',
    name: 'pqrsdf',
    component: () => import(/* webpackChunkName: "pqrsdf" */ '../views/PQRSDF/Pqrsdf.vue')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/autenticaLoginApp',
    name: 'autenticaLoginApp',
    component: () => import(/* webpackChunkName: "autenticaLoginApp" */ '../views/adminApp/AuthAdminApp.vue')
  },
  {
    path: '/principalAdminApp',
    name: 'principalAdminApp',
    component: () => import(/* webpackChunkName: "principalAdminApp" */ '../views/adminApp/PrincipalAdmin.vue')
  },
  {
    path: '/miscontratos',
    name: 'miscontratos',
    component: () => import(/* webpackChunkName: "miscontratos" */ '../views/miscontratos/MisContratos.vue')
  },
  {
    path: '*',
    name: 'notFound',
    component: () => import(/* webpackChunkName: "Notfound" */ '../views/miscontratos/MisContratos.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const self = Vue.prototype
  const adminPages = ['principalAdminApp', 'autenticaLoginApp']
  if (adminPages.includes(to.name)) {
    store.commit('setAppActiva', 'ADM')
  }
  if (store.state) {
    if (store.state.appActiva === 'ADM') {
      const loggedIn = self.$session.exists()
      if (!loggedIn && to.name !== 'autenticaLoginApp') {
        return next({ name: 'autenticaLoginApp' })
      } else if (loggedIn && to.name !== 'principalAdminApp') {
        return next({ name: 'principalAdminApp' })
      } else {
        return next()
      }
    } else {
      return next()
    }
  } else {
    return next()
  }
})

export default router
